import Icon from '@mdi/react';
import moment from 'moment';
import { mdiNpm, mdiTelegram, mdiGithub, mdiInstagram, mdiEmail, mdiTwitter, mdiGitlab, mdiBitbucket } from '@mdi/js';

const Header = () => {
  const currentYearOld = () => {
    const birthDate = moment(new Date(1996, 0, 17, 3, 30));
    const now = moment(new Date());

    return now.diff(birthDate, 'year');
  };

  return (
    <header className="header">
      <div className="header__avatar">
        <img className="header__avatar-img"
          src="https://hsto.org/getpro/moikrug/uploads/user/100/024/364/0/avatar/medium_b927fdb65bb090c8314f17074175b9ee.jpg"
          alt="Avatar"
        />
      </div>
      <div className="header__description">
        <div className="header__description-text">
          <h1>Shekhovtsov&nbsp;Kirill, {currentYearOld()}&nbsp;years&nbsp;old</h1>
          <h2>Frontend&nbsp;(Fullstack) Developer,&nbsp;TeamLead</h2>
          
        </div>

      </div>
    </header >);
}

export default Header;
